@charset "UTF-8";
.increase_control_btn {
    margin:15px 0 0; max-width:206px;height:50px;overflow:hidden;font-weight:700;
    border:1px solid #c5c5c6;justify-content:space-between;display:flex;background-color:$white;
    @include borderRadius(8px);
    input {width:100px; text-align:center; font-size:15px; font-weight:$bold; color:$gray7;}
    .control_btn {
        width:50px; height:100%; padding:3px; position:relative;
        @include boxSizing;
        button {width:100%; height:100%;display:block;}
        &:first-of-type {
            border-right:1px solid #e8e8e9;
            &:after {
                width:10px; height:2px; background-color:$gray7;content:"";display:block;position:absolute; top:50%; left:50%;
                @include transform(translate(-50%, -50%));
            }
        }
        &:last-of-type {
            border-left:1px solid #e8e8e9;
            &:after {
                width:2px; height:10px; background-color:$gray7;content:"";display:block;
                position:absolute; top:50%; left:50%;
                @include transform(translate(-50%, -50%));
            }
            &:before {
                width:10px; height:2px; background-color:$gray7;content:"";display:block;
                position:absolute; top:50%; left:50%;
                @include transform(translate(-50%, -50%));
            }
        }
    }
    @include mob() {
        margin:em(25) 0 0; max-width:em(360); height:em(100); border:em(2) solid #c5c5c6;
        @include borderRadius(em(10));
        input {width:em(160); font-size:em(28); font-weight:$bold; }
        .control_btn {
            width:em(100); height:100%; padding:em(5);
            @include boxSizing;
            &:first-of-type {
                border-right:em(2) solid #e8e8e9;
                &:after {width:em(24); height:em(4);}
            }
            &:last-of-type {
                border-left:em(2) solid #e8e8e9;
                &:after {width:em(4); height:em(24);}
                &:before {width:em(24); height:em(4);}
            }
        }
    }
}
.form_btn_wrap {
    max-width:500px; margin:0 auto;
    .btn_type01 {
        font-size:18px; line-height:58px; text-align:center; font-weight:$medium; background-color:$white; border:1px solid $gray3; border-radius:10px; display:block; box-sizing:border-box; color:$gray7;
        @include transition();
        &:hover, &:focus {
            color:$white; background-color:$gray7; border-color:$gray7;
        }
    }
    .btn_type02 {
        font-size:18px; line-height:58px; text-align:center; font-weight:$medium; background-color:$blue5; border:1px solid $blue5; border-radius:10px; display:block; box-sizing:border-box; color:$white; cursor:pointer;
        @include transition();
        &:hover, &:focus {
            background-color:$blue6; border-color:$blue6;
        }
        &.btn_disabled {background-color:$blue4; border-color:$blue4; cursor:default;
            &:hover, &:focus {
                background-color:$blue4; border-color:$blue4;
            }
        }
    }
    a {
        margin:8px 0 0;
        &:first-child {margin:0;}
    }
    @include mob() {
        max-width:100%;
        .btn_type01 {
            font-size:em(32); line-height:em(116); font-weight:$bold; border-radius:em(10);
        }
        .btn_type02 {
            font-size:em(32); line-height:em(116); font-weight:$bold; border-radius:em(10);
        }
        a {
            margin:em(16) 0 0;
        }
    }
}
// NHA-RETAIL-CHECK OUT-003_shipping
.shipping_form {
    max-width:500px; margin:0 auto; padding:80px 0 100px;
    .btn_type01 {
        font-size:13px; line-height:36px; font-weight:$medium; background-color:$white; border:1px solid $gray3; border-radius:19px; display:block; box-sizing:border-box; color:$gray7; text-align:center; padding:0 10px;
        @include transition();
        &:hover, &:focus {background-color:$gray7; border-color:$gray7; color:$white;}
    }
    .box {
        margin:0 0 80px;
        > .tit_wrap {
            margin:0 0 25px;
            strong {font-size:20px; display:block; color:$gray7; font-weight:$medium;}
        }
        .form_list {
            ul {
                li {
                    margin:8px 0 0;
                    &:first-child {margin:0;}
                    &.half_box {
                        @extend %clearfix;
                        > div {
                            float:left; width:calc(50% - 4px);
                            &:last-child {float:right;}
                        }
                    }
                }
            }
        }
        .half_form {width:246px;}
        .check_01 {margin:30px 0 0;}
        .check_02 {
            margin:20px 0 0; padding:0 20px;
            input[type="checkbox"] + label {color:$gray5;}
            input[type="checkbox"] + label:before {border-color:$gray3;}
            input[type="checkbox"]:hover ~ label:before {border-color:$blue5 !important;}
            input[type="checkbox"]:focus ~ label:before {border-color:$blue5;}
        }
        .terms_info_txt {
            font-size:16px; margin:13px 0 0; color:$gray7; font-weight:$medium;
            button {font-size:16px; text-decoration:underline; color:$blue5; font-weight:$medium;}
        }
        .info_box_01 {margin:40px 0 0;}
        .list_item {
            width:100%; padding:30px 20px 22px; border:2px solid $gray7; border-radius:8px; box-sizing:border-box; position:relative;
            &:first-child {margin:0;}
            .btn_wrap {
                position:absolute; top:50%; right:20px;
                @include translate(0, -50%);
            }
        }
        .name_area {
            @extend %clearfix;
            p {float:left;}
            .name {font-size:16px; color:$gray7; font-weight:$bold;}
            .country {
                font-size:14px; color:$gray5; position:relative; margin:0 0 0 10px; padding:0 0 0 13px;
                &:before {content:"/"; position:absolute; top:0; left:0;}
            }
        }
        .address_area {
            padding:12px 0 0; text-align:left;
            p {font-size:14px; color:$gray7; line-height:22px;}
        }
    }
    .Terms_Conditions_popup {
        .popup_content {
            max-width:460px; padding:40px; box-sizing:border-box; background-color:$white; border-radius:20px;
            > strong {font-size:26px; line-height:36px; text-align:center; font-weight:$medium; color:$gray7; display:block; padding:0 20px;}
            .terms_cont {
                height:400px; margin:30px 0 0; overflow-y:auto;
                .title {
                    border-bottom:1px solid $gray2; padding:0 0 30px;
                    strong {font-size:16px; font-weight:$medium; display:block;}
                    span {font-size:14px; color:$gray5}
                }
                .cont {
                    color:$gray5; padding:30px 0 0;
                    > strong {font-size:16px; margin:0 0 10px; display:block;}
                    > p {font-size:14px; line-height:22px;}
                }
            }
        }
        .btn_popup_close {
            width:24px; height:24px; text-indent:-9999px; position:absolute; top:20px; right:20px;
            &:after {
                background-image:url(../../images/common/close_x_btn.png); content:"";
                @include posCenterSolt();
                @include translate(-50%, -50%);
                @include textBackground(12px, 12px);
                @include backgroundSoltC();
            }
        }
    }
    @include mob() {
        max-width:100%; padding:em(80) em(30) em(100);
        .btn_type01 {font-size:em(26); line-height:em(72); border:em(2) solid $gray3; border-radius:em(38); padding:0 em(20);}
        .box {
            margin:0 0 em(100);
            &:last-child {margin:0 0 em(120);}
            > .tit_wrap {
                margin:0 0 em(26);
                strong {font-size:em(36); font-weight:$bold;}
            }
            .form_list {
                ul {
                    li {margin:em(12) 0 0;}
                }
            }
            .half_form {width:100%;}
            .check_01 {margin:em(40) 0 0;}
            .check_02 {margin:em(24) 0 0; padding:0;}
            .terms_info_txt {
                font-size:em(28); margin:em(22) 0 0; padding:0 0 0 em(5);
                button {font-size:em(28);}
            }
            .info_box_01 {margin:em(54) 0 0;}
            .list_item {
                width:100%; margin:em(16) 0 0; padding:em(56) em(40) em(48); border:em(2) solid $gray3; border-radius:em(16);
                &.active {border:em(4) solid $gray7;}
                .btn_wrap {
                    position:static; padding:em(24) 0 0;
                    @include translate(0, 0);
                    &:after {content:""; display:block; clear:both;}
                }
            }
            .name_area {
                line-height:em(32);
                .name {font-size:em(32);}
                .country {font-size:em(28);margin:0 0 0 em(20); padding:0 0 0 em(26);}
            }
            .address_area {
                padding:em(24) 0 0;
                p {font-size:em(28); line-height:em(36);}
            }
            .form_check_radio_type01 {
                > .tit_wrap {
                    strong {font-weight:$bold;}
                }
                .notification_txt {
                    border-radius:em(16);
                }
                ul {padding:em(24) 0 0;}
            }
        }
        .Terms_Conditions_popup {
            .popup_content {
                max-width:100%; margin:em(30); padding:em(80) em(30); border-radius:em(24);
                > strong {font-size:em(48); line-height:em(60); font-weight:$bold; color:$gray7; padding:0;}
                .terms_cont {
                    height:em(800); margin:em(70) 0 0;
                    .title {
                        border-bottom:em(2) solid $gray2; padding:0 0 em(52);
                        strong {font-size:em(36); font-weight:$bold; padding:0 0 em(16);}
                        span {font-size:em(32); font-weight:$medium;}
                    }
                    .cont {
                        padding:em(40) 0 0;
                        > strong {font-size:em(36); line-height:em(48); margin:0 0 em(24); font-weight:$medium;}
                        > p {font-size:em(32); line-height:em(48); font-weight:$medium;}
                    }
                }
            }
            .btn_popup_close {
                width:em(72); height:em(72); top:em(22); right:em(22);
                &:after {
                    width:em(72); height:em(72); background-size:em(72) em(72); background-image:url(../../images/common/close_x_btn_m.png);
                }
            }
        }
    }
}
