// NHA-COMMON-Managed Services check out-081_T1
.payment_form {
    max-width:1200px; margin:0 auto; padding:80px 0;
    .box {
        padding:70px 0; border-bottom:2px solid $gray2;
        &:first-child {padding:0 0 70px;}
        &:last-of-type {border:none; padding:70px 0 100px;}
        > strong {display:block; font-size:26px; line-height:26px; margin:0 0 30px; font-weight:$medium; color:$gray7}
        .radio_type01 {width:calc(50% - 100px)}
        .title {
            margin:0 0 30px;
            strong {display:inline-block; vertical-align:middle; font-size:26px; line-height:26px; font-weight:$medium; color:$gray7}
            .btn_change {
                display:inline-block; vertical-align:middle; color:$gray6; font-size:15px; border:1px solid $gray2;
            }
        }
    }
    .methods {
        margin:36px 0 0; border-top:1px solid $gray2; padding:36px 0 0;
        > div {display:none;}
        > div:first-child {display:block;}
        li {
            display:flex; margin:8px 0 0;
            &:first-child {margin:0;}
            .form_text_type01 {width:360px; margin:0 10px 0 0;}
            &.field_email {
                .form_text_type01 {
                    width:488px;
                    div {width:360px; display:inline-block; vertical-align:middle;}
                }
                button {
                    width:58px; height:58px; border:1px solid $gray2; vertical-align:middle; border-radius:10px; text-indent:-99999px;
                    @include backgroundSolt();
                    &.btn_plus_field {background-image:url(../../images/common/plus_icon.png)}
                    &.btn_minus_field {background-image:url(../../images/common/minus_icon.png)}
                    &.btn_disabled {background-color:$gray1; cursor:pointer;}
                }
            }
            &.field_long {
                .form_text_type01 {width:100%; margin:0;}
            }
        }
    }
    .promotion {
        display:flex;
        .form_text_type01 {width:388px; margin:0 8px 0 0;}
        button {
            width:104px; font-size:18px; line-height:58px; text-align:center; font-weight:$medium; background-color:$blue5; border:1px solid $blue5; border-radius:10px; display:block; box-sizing:border-box; color:$white; cursor:pointer;
            @include transition();
            &:hover, &:focus {
                background-color:$blue6; border-color:$blue6;
            }
            &.btn_disabled {background-color:$blue4; border-color:$blue4; cursor:default;
                &:hover, &:focus {
                    background-color:$blue4; border-color:$blue4;
                }
            }
        }
    }
    .prd_info {
        padding:0 40px 0 0;
        &:after {display:block; clear:both; content:"";}
        p {
            width:450px; float:left;
            strong {font-size:28px; display:block; color:$gray7; font-weight:$bold;}
            em {font-size:13px; padding:7px 12px; border-radius:20px; margin:15px 0 0; display:inline-block; color:$gray5; font-weight:$medium; background-color:$gray1;}
        }
        div {
            width:calc(100% - 450px); float:left; font-size:24px; line-height:28px; text-align:right; color:$gray7; font-weight:$medium; display:flex;
            * {width:33.333%;}
        }
    }
    .info_wrap {
        border-top:1px solid $gray2; margin:40px 0 0;
        .folding_menu {
            border-bottom:1px solid $gray2;
            > button {
                width:100%; padding:23px 0; position:relative; box-sizing:border-box; text-align:left;
                span {color:$gray5; font-size:16px; font-weight:$medium;}
                strong {
                    color:$gray7; font-size:16px; font-weight:$medium; position:absolute; top:50%; right:45px; text-align:right;
                    @include translate(0, -50%);
                }
                &:after {
                    width:13px; height:8px; content:""; position:absolute; top:50%; right:0; background-image:url(../../images/common/select_arr_default.png);
                    @include backgroundSoltC();
                    @include translate(0, -50%);
                    @include transition();
                }
                &.on:after {
                    @include transform(translate(0, -50%) rotate(180deg));
                }
            }
            .cont {display:none; background-color:$gray1; padding:40px;}
        }
    }
    .total_amount {
        dl {
            border-bottom:1px solid $gray2; padding:20px 0; font-size:16px; font-weight:$medium;
            &:after {content:""; display:block; clear:both;}
        }
        dt {width:300px; float:left; color:$gray5;}
        dd {width:calc(100% - 300px); padding:0 15px 0 0; float:left; text-align:right; color:$gray7; box-sizing:border-box;}
        dl:first-child {padding:0 0 20px;}
        dl:last-child {
            border:none;
            dt, dd {color:$gray7; font-size:20px;}
        }
    }
    @include mob() {
        padding:em(80) em(30);
        .box {
            padding:em(60) 0;
            &:first-child {padding:0 0 em(60);}
            &:last-of-type {padding:em(60) 0 em(120);}
            > strong {font-size:em(36); line-height:em(36); margin:0 0 em(22); font-weight:$bold;}
            .radio_type01 {width:100%}
        }
        .methods {
            margin:em(40) 0 0; padding:em(40) 0 0;
            li {
                margin:em(12) 0 0; display:block;
                .form_text_type01 {
                    width:100%; margin:em(12) 0 0;
                    &:first-child {margin:0;}
                }
                &.field_email {
                    .form_text_type01 {
                        width:100%;
                        div {width:calc(100% - 13.25rem)}
                    }
                    button {
                        width:em(120); height:em(120); border-radius:em(10); background-size:em(24) !important;
                        &.btn_plus_field {background-image:url(../../images/common/plus_icon_m.png)}
                        &.btn_minus_field {background-image:url(../../images/common/minus_icon_m.png)}
                    }
                }
                &.field_long {
                    .form_text_type01 {width:100%; margin:0;}
                }
            }
        }
        .promotion {
            .form_text_type01 {width:100%; margin:0 em(14) 0 0;}
            button {
                width:em(260); font-size:em(32); line-height:em(116); font-weight:$bold; border-radius:em(10);
            }
        }
        .prd_info {
            padding:0;
            p {
                width:100%;
                strong {font-size:em(42);}
                em {font-size:em(26); padding:em(14) em(24); border-radius:em(20); margin:em(20) 0 0; font-weight:$bold;}
            }
            div {
                width:100%; font-size:em(34); line-height:em(42); font-weight:$bold; padding:em(30) 0 0;
                span:nth-of-type(1) {width:20%}
                span:nth-of-type(2) {width:40%}
                strong {width:40%}
            }
        }
        .total_amount {
            dl {
                padding:em(40) 0; font-size:em(32); font-weight:$bold;
            }
            dt {width:em(400);}
            dd {width:calc(100% - 20rem); padding:0;}
            dl:first-child {padding:em(40) 0;}
            dl:last-child {
                dt, dd {font-size:em(40);}
            }
        }
    }
}
