//NHA-COMMON-FEEDBACK(P)-038
.popup.feedback_popup {
  .popup_content {
    max-width:460px; text-align:center; background-color:$white; border-radius:24px;
    .title {
      padding:54px 0 0;
      strong {font-size:30px; color:$gray7; font-weight:$medium;}
    }
  }
  .rating_stars {
    ul {
      display:inline-block;
      @extend %clearfix;
      li:nth-last-of-type(even) {
        padding:0 0 0 10px;
        &:first-child {padding:0;}
        button {background-image:url(../../images/my_menu/feedback_star_ico_l.png);}
        &.hover {
          button {background-image:url(../../images/my_menu/feedback_star_ico_color_l.png);}
        }
        &.selected {
          button {background-image:url(../../images/my_menu/feedback_star_ico_color_l.png);}
        }
      }
      li:nth-last-of-type(odd) {
        button {background-image:url(../../images/my_menu/feedback_star_ico_r.png);}
        &.hover {
          button {background-image:url(../../images/my_menu/feedback_star_ico_color_r.png);}
        }
        &.selected {
          button {background-image:url(../../images/my_menu/feedback_star_ico_color_r.png);}
        }
      }
    }
    li {
      float:left;
      button {width:18px; height:34px; text-indent:-9999px; display:block; cursor:pointer;}
    }
  }
  .feedback_form {
    padding:25px 40px 50px;
    textarea {margin:32px 0 0;}
  }
  .btn_type01 {
    font-size:16px; line-height:58px; font-weight:$bold; background-color:$blue5; border:1px solid $blue5; border-radius:8px; display:block; box-sizing:border-box; color:$white; cursor:pointer; margin:20px 0 0;
    @include transition();
    &:hover, &:focus {background-color:$blue6; border-color:$blue6;}
    &.btn_disabled {
      background-color:$blue4; border-color:$blue4; cursor:default;
      &:hover, &:focus {background-color:$blue4; border-color:$blue4;}
    }
  }
